import { Card, Grid, styled, useTheme, Icon, IconButton, TextField, Box, Button } from '@mui/material';
import { Fragment, useEffect, useState } from 'react';
import StatCards from './StatCards';
import TopSellingTable from './TopSellingTable';
import KeyValueCards from 'app/views/dashboard/shared/KeyValueCards';
import { useSelector } from 'react-redux';
import Parse from 'app/vendor/Parse';
import { ca } from 'date-fns/locale';
import NumberConsole from 'app/components/NumberConsole'
import { showModal } from 'app/redux/actions/ApplicationActions';
import { useDispatch } from 'react-redux';


const ContentBox = styled('div')(({ theme }) => ({
  margin: '30px',
  [theme.breakpoints.down('sm')]: { margin: '16px' },
}));

const Title = styled('span')(() => ({
  fontSize: '1rem',
  fontWeight: '500',
  marginRight: '.5rem',
  textTransform: 'capitalize',
}));

const SubTitle = styled('span')(({ theme }) => ({
  fontSize: '0.875rem',
  color: theme.palette.text.secondary,
}));

const H4 = styled('h4')(({ theme }) => ({
  fontSize: '1rem',
  fontWeight: '500',
  marginBottom: '16px',
  textTransform: 'capitalize',
  color: theme.palette.text.secondary,
}));

const Analytics = ({ itemKey, itemColumns = [] }) => {
  const { palette } = useTheme();
  const [items, setItems] = useState([])
  const [activeItem, setActiveItem] = useState(null)
  const [activeactiveItemIndex, setActiveactiveItemIndex] = useState(0)

  const refreshData = async () => {
    const res = await Parse.Cloud.run('getItemsMeta', {
      key: itemKey
    })
    setItems(res)
  }

  const handleDelete = async (row) => {
    await Parse.Cloud.run('deleteItems', {
      ids: [row?.objectId]
    })
    refreshData();
  }
  const columns = [
    {
      header: 'ID',
      render: row => row?.objectId
    },
    {
      header: 'Name',
      render: row => row?.name
    },
    {
      header: 'Created Date',
      render: row => row?.createdAt
    },
    {
      header: 'type',
      render: row => row?.type
    },

    ...itemColumns,
    {
      header: 'Action',
      render: row => <>
        <IconButton onClick={() => {
          setActiveItem(row)
          dispatch(showModal({
            content: <>
              {
                JSON.stringify(activeItem)
              }
            </>
          }))
        }

        }>
          <Icon color="primary">remove_red_eye</Icon>
        </IconButton>

        <IconButton onClick={() => setActiveItem(row)}>
          <Icon color="primary">search</Icon>
        </IconButton>
        <IconButton onClick={() => {
          handleDelete(row)
          refreshData()
        }}><Icon color="primary">delete</Icon></IconButton>

      </>
    }
  ]
  const [tagData, setTagData] = useState({})
  const [newTag, setNewTag] = useState({})

  const tagItem = async (id, key, value) => {
    console.log(key)
    return await Parse.Cloud.run('tagItem', {
      id,
      key,
      value
    })
  }
  const dispatch = useDispatch()

  useEffect(async () => {
    refreshData()
  }, [])

  useEffect(() => {
    setActiveItem(items?.[activeactiveItemIndex])
  }, [activeactiveItemIndex, items])

  return (
    <Fragment>
      <ContentBox className="analytics">
        <Grid container spacing={3}>
          <Grid item lg={12} md={12} sm={12} xs={12}>
            <KeyValueCards
              itemKey={itemKey}
              onUpdate={() => refreshData()}
            />
          </Grid>
          <Grid item lg={12} md={12} sm={12} xs={12}>
            <h2>{activeItem?.objectId}'s Tag  <IconButton><Icon>add</Icon></IconButton></h2>
            <Box>
              <TextField
                autoFocus={true}
                type="text"
                name="firstName"
                label="Key"
                onChange={(e) => {
                  setNewTag({
                    newTag,
                    key: e.target.value
                  })
                }}
                value={newTag.key}
                validators={["required"]}
                errorMessages={["this field is required"]}
              />
              <TextField
                autoFocus={true}
                type="text"
                name="firstName"
                label="Value"
                onChange={(e) => {
                  setNewTag({
                    ...newTag,
                    value: e.target.value
                  })
                }}
                value={newTag.value}
                validators={["required"]}
                errorMessages={["this field is required"]}
              />
              <Box display="flex">
                <Button
                  variant="contained"
                  onClick={() => { tagItem(activeItem.objectId, newTag.key, newTag.value) }}
                >Tag Item</Button>
              </Box>
            </Box>
            <StatCards
              onClickDetails={(card) => {
                setTagData(card?.item)
              }}
              cardList={activeItem?.tag ? Object.keys(activeItem?.tag).map(k => ({
                name: k,
                amount: Array.isArray(activeItem?.tag[k]) ? activeItem?.tag[k].length : activeItem?.tag[k],
                item: activeItem?.tag[k]
              })) : []}
            />
            <NumberConsole
              dataArr={tagData}
              handleClick={(data) => {

                dispatch(showModal({
                  content: <>
                    {JSON.stringify(data)}
                    <img src={data.source} width="400px" />

                  </>
                }))


              }}
              pageSize={12}
              mask={'yyyyyyyy'}
              displaySource
            />
            {
              <TopSellingTable
                rows={items}
                columns={columns}
              />
            }
          </Grid>

        </Grid>
      </ContentBox>
    </Fragment>
  );
};

export default Analytics;
