import { Box, Card, Grid, Icon, IconButton, styled, Tooltip, TextField, Input, Button } from '@mui/material';
import { Small } from 'app/components/Typography';
import Parse from 'app/vendor/Parse'
import { useEffect, useState } from "react"
import { getBase64, getJSON } from 'app/helpers/file'
import { chunk } from 'app/helpers/array'
import { useDispatch } from 'react-redux';
import { setApplicationMessage } from 'app/redux/actions/ApplicationActions';
import Menu from "app/components/Menu"
import { showLoader, hideLoader } from 'app/redux/actions/ApplicationActions';

const StyledCard = styled(Card)(({ theme }) => ({
  display: 'flex',
  flexWrap: 'wrap',
  alignItems: 'center',
  justifyContent: 'space-between',
  padding: '24px !important',
  background: theme.palette.background.paper,
  [theme.breakpoints.down('sm')]: { padding: '16px !important' },
}));

const ContentBox = styled(Box)(({ theme }) => ({
  display: 'flex',
  flexWrap: 'wrap',
  alignItems: 'center',
  '& small': { color: theme.palette.text.secondary },
  '& .icon': { opacity: 0.6, fontSize: '44px', color: theme.palette.primary.main },
}));

const Heading = styled('h6')(({ theme }) => ({
  margin: 0,
  marginTop: '4px',
  fontSize: '14px',
  fontWeight: '500',
  color: theme.palette.primary.main,
}));

const StatCards = ({ itemKey, hideSuperAdminFunction = true, onUpdate }) => {

  const dispatch = useDispatch()
  const [payload, setPayload] = useState({
    key: itemKey,
    file: ''
  })
  const cardList = [
    { name: 'New Leads', amount: 3050, icon: 'key' },
  ];




  const handleUpload = async (payload) => {
    dispatch(showLoader())
    if (payload.file || payload.text) {
      const json = payload.text || await getJSON(payload.file);
      if (json) {
        const res = await Parse.Cloud.run('uploadJSON', {
          key: payload.key || payload.file.name,
          json,
          name: payload.file.name
        })
        dispatch(hideLoader())
        dispatch(setApplicationMessage({
          text: res.message
        }))
        onUpdate && onUpdate()
      } else {
        dispatch(setApplicationMessage({
          text: "Invalid File"
        }))
      }

    }
  }

  const handlePurge = async (payload) => {
    const res = await Parse.Cloud.run('deletePublicItems', {
      key: payload.key
    })
  }



  return (
    <Grid container spacing={3} sx={{ mb: '24px' }}>
      {cardList.map((item, index) => (
        <Grid item xs={12} md={12} key={index}>
          <StyledCard elevation={6}>
            <ContentBox>
              <Icon className="icon">{item.icon}</Icon>
              <Box ml="12px" display="flex" gap="10px">
                {
                  !hideSuperAdminFunction &&
                  <Menu
                    data={[{
                      label: 'Pick One',
                      value: ''
                    }, {
                      label: 'public-q',
                      value: 'public-q'
                    }]}
                    titleKey="label"

                    onItem={e => {
                      setPayload({
                        ...payload,
                        key: e.value
                      })
                    }}
                  />

                }
                <TextField
                  autoFocus={true}
                  type="text"
                  name="firstName"
                  label="Key"
                  onChange={(e) => {
                    setPayload({
                      ...payload,
                      key: e.target.value
                    })
                  }}
                  value={payload.key}
                  validators={["required"]}
                  errorMessages={["this field is required"]}
                />
                <TextField
                  autoFocus={true}
                  type="file"
                  name="firstName"

                  onChange={(event) => {
                    setPayload({
                      ...payload,
                      file: event.target.files[0]
                    })
                  }}
                  value={""}
                  validators={["required"]}
                  errorMessages={["this field is required"]}
                />

                <TextField
                  autoFocus={true}
                  type="text"
                  name="firstName"
                  label="value"
                  onChange={(event) => {
                    setPayload({
                      ...payload,
                      text: {
                        value: event.target.value
                      }
                    })
                  }}
                  value={payload?.text?.value}
                  validators={["required"]}
                  errorMessages={["this field is required"]}
                />

                <Box display="flex">
                  <Button
                    variant="contained"
                    onClick={() => handleUpload(payload)}
                  >Upload</Button>
                </Box>

                {

                  !hideSuperAdminFunction && <Button
                    variant="contained"
                    onClick={() => handlePurge(payload)}
                  >Purge</Button>
                }
              </Box>
            </ContentBox>

          </StyledCard>
        </Grid>
      ))}
    </Grid>
  );
};

export default StatCards;
